  z-index: 100;
}

ion-title {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}

.firstImg-set {
  background-color: rgb(148, 165, 214);
  margin-top: 10px;
  border-radius: 20px;
  margin-left: 10px;
  margin-right: 10px;
}

.image-set {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100px;
  height: 100px;
}

.secDiv {
  border-bottom: 0.5px solid rgb(61, 57, 57);
  margin-left: 5px;
  margin-right: 5px;
}

.thrDiv {
  margin-top: 20px;
  height: 20px;
  margin-right: -50px !important;
  margin-left: 15px;
  padding-right: 10px;
  width: 100%;
  background: #ddd;
  text-align: center;
}

.progress-line {
  height: 20px;
  border-radius: 20px;
  background: #152caf;
}

.image-sett {
  width: 30px;
  height: 30px;
  text-align: center;
}

.container {
  position: relative;
  margin-top: 20px;
}

.smallProgress {
  display: block;
  width: 230px;
  height: 30px;
  background: #152caf;
  position: absolute;
}

.tierShow {
  background: chocolate;
  width: 400px;
  height: 60px;
  z-index: 9;
}
